	module.exports = {"data":{"challengeNode":{"title":"Taste the Bootstrap Button Color Rainbow","description":["<code>btn-primary</code> class 的颜色是你在应用中的主题色。这样 “突出显示” 是引导用户按步就班进行操作的有效办法。","将按钮的 class 从 Bootstrap 的 <code>btn-default</code> 替换为 <code>btn-primary</code>。","记住：你的按钮仍然需要 <code>btn</code> 和 <code>btn-block</code> class。"],"challengeType":0,"fields":{"slug":"/front-end-libraries/bootstrap/taste-the-bootstrap-button-color-rainbow","blockName":"Bootstrap","tests":[{"text":"按钮的 class 属性应该有 <code>btn-primary</code>。","testString":"assert($(\"button\").hasClass(\"btn-primary\"), '按钮的 class 属性应该有 <code>btn-primary</code>。');"},{"text":"按钮的 class 属性应该仍有 <code>btn</code> 和 <code>btn-block</code>。","testString":"assert($(\"button\").hasClass(\"btn-block\") && $(\"button\").hasClass(\"btn\"), '按钮的 class 属性应该仍有 <code>btn</code> 和 <code>btn-block</code>。');"},{"text":"确保所有 <code>button</code> 元素都有一个闭合标签。","testString":"assert(code.match(/<\\/button>/g) && code.match(/<button/g) && code.match(/<\\/button>/g).length === code.match(/<button/g).length, '确保所有 <code>button</code> 元素都有一个闭合标签。');"}]},"required":[{"link":"https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/css/bootstrap.css","raw":null,"src":null}],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<link href=\"https://fonts.googleapis.com/css?family=Lobster\" rel=\"stylesheet\" type=\"text/css\">\n<style>\n  .red-text {\n    color: red;\n  }\n\n  h2 {\n    font-family: Lobster, Monospace;\n  }\n\n  p {\n    font-size: 16px;\n    font-family: Monospace;\n  }\n\n  .thick-green-border {\n    border-color: green;\n    border-width: 10px;\n    border-style: solid;\n    border-radius: 50%;\n  }\n\n  .smaller-image {\n    width: 100px;\n  }\n</style>\n\n<div class=\"container-fluid\">\n  <h2 class=\"red-text text-center\">CatPhotoApp</h2>\n\n  <p>Click here for <a href=\"#\">cat photos</a>.</p>\n\n  <a href=\"#\"><img class=\"smaller-image thick-green-border\" src=\"https://bit.ly/fcc-relaxing-cat\" alt=\"A cute orange cat lying on its back.\"></a>\n\n  <img src=\"https://bit.ly/fcc-running-cats\" class=\"img-responsive\" alt=\"Three kittens running towards the camera.\">\n  <button class=\"btn btn-default btn-block\">Like</button>\n  <p>Things cats love:</p>\n  <ul>\n    <li>cat nip</li>\n    <li>laser pointers</li>\n    <li>lasagna</li>\n  </ul>\n  <p>Top 3 things cats hate:</p>\n  <ol>\n    <li>flea treatment</li>\n    <li>thunder</li>\n    <li>other cats</li>\n  </ol>\n  <form action=\"/submit-cat-photo\">\n    <label><input type=\"radio\" name=\"indoor-outdoor\"> Indoor</label>\n    <label><input type=\"radio\" name=\"indoor-outdoor\"> Outdoor</label>\n    <label><input type=\"checkbox\" name=\"personality\"> Loving</label>\n    <label><input type=\"checkbox\" name=\"personality\"> Lazy</label>\n    <label><input type=\"checkbox\" name=\"personality\"> Crazy</label>\n    <input type=\"text\" placeholder=\"cat photo URL\" required>\n    <button type=\"submit\">Submit</button>\n  </form>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[{"link":"https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/css/bootstrap.css","raw":null,"src":null}],"nextChallengePath":"/front-end-libraries/bootstrap/call-out-optional-actions-with-btn-info","id":"bad87fee1348cd8acef08811"},"slug":"/front-end-libraries/bootstrap/taste-the-bootstrap-button-color-rainbow"}}

